@import url(https://fonts.googleapis.com/css?family=Allerta+Stencil);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

html {
  font-size: 62.5%; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* All focus */
a:focus,
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: none; }

html {
  font-size: 62.5%; }

button,
input,
textarea {
  color: #000;
  background: transparent;
  border: 0; }

* {
  box-sizing: border-box; }

.Timeline svg .year line, .Timeline svg .year circle {
  transition: all 0.2s ease; }

body {
  font-family: "Avenir", sans-serif;
  scroll-behavior: smooth;
  background: #999; }

.Timeline svg {
  margin: 10px; }
  .Timeline svg .year line {
    stroke: #888;
    stroke-width: 4;
    cursor: pointer; }
    .Timeline svg .year line:hover {
      stroke: #777;
      stroke-width: 12; }
      .Timeline svg .year line:hover circle {
        fill: red; }
  .Timeline svg .year circle {
    fill: #666; }
  .Timeline svg .year circle[data-label="0"] {
    fill: #ff9999; }
  .Timeline svg .year circle[data-label="1"] {
    fill: #ffee99; }
  .Timeline svg .year circle[data-label="2"] {
    fill: #bbff99; }
  .Timeline svg .year circle[data-label="3"] {
    fill: #99ffcc; }
  .Timeline svg .year circle {
    fill: red; }

.peoples {
  position: absolute;
  left: -10px; }

